"use client";

import React from "react";
import Image from "next/image";
import { Search } from "lucide-react";

const HeroSection: React.FC = () => {
  return (
    <section className="w-full bg-gray-50">
      {/* HERO TOP SECTION */}
      <div className="flex flex-row items-center justify-between flex-wrap px-4 sm:px-6 md:px-12 lg:px-20 py-10 gap-4 sm:gap-8 md:gap-12">
        {/* Left Side — Text & Search */}
        <div className="flex flex-col justify-center w-full sm:w-[45%] text-left space-y-5">
          <h1 className="text-2xl sm:text-3xl md:text-4xl lg:text-5xl font-extrabold text-gray-900 leading-tight">
            Find Your Perfect Ride
          </h1>

          <p className="text-gray-600 text-sm sm:text-base md:text-lg max-w-md">
            Premium vehicles available for rent with instant booking.
          </p>

          {/* Search Box */}
          <div className="flex items-center w-full max-w-md bg-white rounded-full shadow-md border border-gray-200 mt-3 sm:mt-5">
            <input
              type="text"
              placeholder="Mercedes Benz GLE"
              className="flex-grow px-4 py-2 sm:py-3 rounded-l-full text-gray-700 focus:outline-none text-sm sm:text-base"
            />
            <button className="bg-gray-900 text-white rounded-r-full px-3 sm:px-4 py-2 sm:py-3 flex items-center justify-center hover:bg-gray-800 transition-all duration-200">
              <Search size={18} />
            </button>
          </div>
        </div>

        {/* The hero-section image */}
        <div className="w-full sm:w-[50%] flex justify-center">
          <Image
            src="/images/team-m.png" 
            alt="Car"
            width={600}
            height={400}
            className="object-contain w-full max-w-[320px] sm:max-w-[400px] md:max-w-[500px] lg:max-w-[600px] h-auto"
            priority
          />
        </div>
      </div>

      {/* RESERVATION SECTION */}
      <div className="w-full px-4 sm:px-6 md:px-12 lg:px-20 pb-12">
        <div className="bg-white rounded-2xl shadow-lg border border-gray-200 p-6 md:p-8">
          <h2 className="text-2xl md:text-3xl font-bold text-center mb-6">
            Reserve a Car Now!
          </h2>

          {/* Reservation Form */}
          <form className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-4 gap-4">
            {/* Dropdowns */}
            <select
              aria-label="Select Car Brand"
              className="border border-gray-300 rounded-lg px-4 py-3 focus:ring-2 focus:ring-gray-900 focus:outline-none text-sm md:text-base">
              <option>Lamborghini</option>
              <option>Mercedes</option>
              <option>Audi</option>
              <option>Tesla</option>
            </select>

            <select
              aria-label="Select Car Model"
              className="border border-gray-300 rounded-lg px-4 py-3 focus:ring-2 focus:ring-gray-900 focus:outline-none text-sm md:text-base">
              <option>Huracán</option>
              <option>GLE</option>
              <option>Model S</option>
            </select>

            <select
              aria-label="Select Car Type"
              className="border border-gray-300 rounded-lg px-4 py-3 focus:ring-2 focus:ring-gray-900 focus:outline-none text-sm md:text-base">
              <option>Exotic</option>
              <option>Luxury</option>
              <option>SUV</option>
            </select>

            <select
              aria-label="Select Car Year"
              className="border border-gray-300 rounded-lg px-4 py-3 focus:ring-2 focus:ring-gray-900 focus:outline-none text-sm md:text-base">
              <option>2022</option>
              <option>2023</option>
              <option>2024</option>
            </select>

            <select
              aria-label="Select Price Range"
              className="border border-gray-300 rounded-lg px-4 py-3 focus:ring-2 focus:ring-gray-900 focus:outline-none text-sm md:text-base">
              <option>$1200–$1500</option>
              <option>$1500–$2000</option>
              <option>$2000–$2500</option>
            </select>

            {/* Date Pickers */}
            <input
              aria-label="Inpute Date"
              type="date"
              className="border border-gray-300 rounded-lg px-4 py-3 focus:ring-2 focus:ring-gray-900 focus:outline-none text-sm md:text-base"
            />
            <input
              aria-label="Inpute Date"
              type="date"
              className="border border-gray-300 rounded-lg px-4 py-3 focus:ring-2 focus:ring-gray-900 focus:outline-none text-sm md:text-base"
            />

            {/* Reserve Button */}
            <button
              type="submit"
              className="bg-gray-900 text-white px-6 py-3 rounded-lg flex items-center justify-center hover:bg-gray-800 transition-all duration-200 font-semibold col-span-2 md:col-span-4 lg:col-span-1"
            >
              Reserve now →
            </button>
          </form>
        </div>
      </div>
    </section>
  );
};

export default HeroSection;